import CustomPhoneField from "./CustomPhoneField";
import CustomSelect from "./CustomSelect";
import { CustomTextField } from "./CustomTextField";

interface FormField {
  id: number;
  component: JSX.Element;
};

export interface SelectInterface {
  option: string;
  value: string;
};

const usersGroupOptions: string[] = ["-1", "-2", "-3"];
const languageOptions: string[] = ["", "", ""];

const newFieldOptions: string[] = ["Te-1", "Te-2", "Te-3"];

export const FormFields: FormField[] = [
  {
    id: 1,
    component: (
      <CustomTextField
        name="order"
        type="text"
        placeholder=" "
        label=""
      />
    ),
  },
  {
    id: 2,
    component: (
      <CustomTextField
        name="fullName"
        type="text"
        placeholder=" "
        label="  "
      />
    ),
  },
  {
    id: 3,
    component: (
      <CustomPhoneField
        name="phoneNumber"
        type="tel"
        placeholder="1 (999) 999-9999"
        label=" "
      />
    ),
  },
  {
    id: 4,
    component: (
      <CustomTextField
        name="email"
        type="email"
        placeholder="example@mail.com"
        label="Email"
      />
    ),
  },
  {
    id: 5,
    component: (
      <CustomSelect
        name="usersGroup"
        type="select"
        label=" "
        hint=" "
        options={usersGroupOptions}
      />
    ),
  },
  {
    id: 6,
    component: (
      <CustomSelect
        name="language"
        type="select"
        label=""
        hint=" "
        options={languageOptions}
      />
    ),
  },
  {
    id: 7,
    component: (
      <CustomSelect
        name="newField"
        type="select"
        label="  "
        hint=""
        options={newFieldOptions}
      />
    ),
  },
  {
    id: 8,
    component: (
      <CustomTextField
        name="fieldValue"
        type="text"
        placeholder=" "
        label=" "
      />
    ),
  },
];
